import { useState, useEffect, createContext, useContext } from 'react';
import { useQuery, useMutation, useQueryClient, useInfiniteQuery } from '@tanstack/react-query';
import { ChatMessage, CreateChatMessageRequest } from '../types';
import { chatService } from '../services/api';

// 聊天上下文类型定义
interface ChatContextType {
  // 当前聊天对话
  currentChat: {
    orderId: string;
    userId: string;
    userName: string;
  } | null;
  // 消息列表
  messages: ChatMessage[];
  // 未读消息数量
  unreadCount: number;
  // 发送消息
  sendMessage: (content: string, type?: 'text' | 'image' | 'location') => Promise<void>;
  // 加载更多消息
  loadMoreMessages: () => void;
  // 设置当前聊天
  setCurrentChat: (chat: { orderId: string; userId: string; userName: string } | null) => void;
  // 标记消息已读
  markMessageAsRead: (messageId: string) => Promise<void>;
  // 是否有更多消息
  hasMoreMessages: boolean;
  // 加载状态
  isLoading: boolean;
  // 错误信息
  error: Error | null;
}

const ChatContext = createContext<ChatContextType | undefined>(undefined);

// 聊天提供者组件
export const ChatProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const queryClient = useQueryClient();
  const [currentChat, setCurrentChat] = useState<{ orderId: string; userId: string; userName: string } | null>(null);

  // WebSocket连接状态
  const [isConnected, setIsConnected] = useState(false);

  // 获取未读消息数量
  const { data: unreadCountData } = useQuery({
    queryKey: ['unreadMessages'],
    queryFn: () => chatService.getUnreadCount(),
    refetchInterval: 30000, // 每30秒刷新一次
  });

  // 获取当前聊天消息（分页加载）
  const {
    data: messagesData,
    fetchNextPage,
    hasNextPage,
    isFetchingNextPage,
    isLoading,
    error,
  } = useInfiniteQuery({
    queryKey: ['chatMessages', currentChat?.orderId],
    queryFn: ({ pageParam = 1 }) => {
      if (!currentChat?.orderId) {
        throw new Error('No active chat');
      }
      return chatService.getChatMessages(currentChat.orderId);
    },
    initialPageParam: 1,
    getNextPageParam: (lastPage, allPages) => {
      // 这里需要根据实际API响应结构调整
      const totalMessages = lastPage.data?.length || 0;
      const loadedMessages = allPages.reduce((acc, page) => acc + (page.data?.length || 0), 0);
      
      // 假设每页20条消息，如果还有更多则返回下一页
      if (totalMessages === 20 && loadedMessages < 100) { // 假设最多100条
        return allPages.length + 1;
      }
      return undefined;
    },
    enabled: !!currentChat?.orderId,
    staleTime: 5 * 60 * 1000, // 5分钟
  });

  // 发送消息mutation
  const sendMessageMutation = useMutation({
    mutationFn: (data: {
      orderId: string;
      receiverId: string;
      message: string;
      type: 'text' | 'image' | 'location';
    }) => chatService.sendMessage({
      orderId: data.orderId,
      receiverId: data.receiverId,
      message: data.message,
      type: data.type,
    }),
    onSuccess: () => {
      // 刷新当前聊天消息
      queryClient.invalidateQueries({ queryKey: ['chatMessages', currentChat?.orderId] });
      // 刷新未读消息数量
      queryClient.invalidateQueries({ queryKey: ['unreadMessages'] });
    },
  });

  // 标记消息已读mutation
  const markAsReadMutation = useMutation({
    mutationFn: (messageId: string) => chatService.markAsRead(messageId),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['unreadMessages'] });
    },
  });

  // WebSocket连接和消息监听
  useEffect(() => {
    if (!currentChat?.orderId) return;

    // 创建WebSocket连接
    const wsUrl = `${process.env.REACT_APP_WS_URL || 'ws://localhost:3001'}/chat/${currentChat.orderId}`;
    const ws = new WebSocket(wsUrl);

    ws.onopen = () => {
      setIsConnected(true);
      console.log('WebSocket连接已建立');
    };

    ws.onmessage = (event) => {
      try {
        const message = JSON.parse(event.data);
        // 添加新消息到缓存
        queryClient.setQueryData(['chatMessages', currentChat.orderId], (oldData: any) => {
          if (!oldData) return oldData;
          
          const newPages = oldData.pages.map((page: any) => ({
            ...page,
            data: [...page.data, message]
          }));
          
          return {
            ...oldData,
            pages: newPages,
          };
        });

        // 如果是接收到的消息，标记为已读
        if (message.senderId !== currentChat.userId) {
          setTimeout(() => {
            markMessageAsRead(message.id);
          }, 1000);
        }
      } catch (error) {
        console.error('解析消息失败:', error);
      }
    };

    ws.onclose = () => {
      setIsConnected(false);
      console.log('WebSocket连接已关闭');
    };

    ws.onerror = (error) => {
      console.error('WebSocket错误:', error);
    };

    return () => {
      if (ws.readyState === WebSocket.OPEN) {
        ws.close();
      }
    };
  }, [currentChat?.orderId, queryClient]);

  // 发送消息函数
  const sendMessage = async (content: string, type: 'text' | 'image' | 'location' = 'text') => {
    if (!currentChat?.orderId || !currentChat?.userId) {
      throw new Error('No active chat');
    }

    try {
      await sendMessageMutation.mutateAsync({
        orderId: currentChat.orderId,
        receiverId: currentChat.userId,
        message: content,
        type,
      });
    } catch (error) {
      console.error('发送消息失败:', error);
      throw error;
    }
  };

  // 加载更多消息
  const loadMoreMessages = () => {
    if (hasNextPage && !isFetchingNextPage) {
      fetchNextPage();
    }
  };

  // 标记消息已读
  const markMessageAsRead = async (messageId: string) => {
    try {
      await markAsReadMutation.mutateAsync(messageId);
    } catch (error) {
      console.error('标记消息已读失败:', error);
    }
  };

  // 获取所有消息
  const messages = messagesData?.pages.flatMap(page => page.data || []) || [];

  const value: ChatContextType = {
    currentChat,
    messages,
    unreadCount: unreadCountData?.data || 0,
    sendMessage,
    loadMoreMessages,
    setCurrentChat,
    markMessageAsRead,
    hasMoreMessages: hasNextPage || false,
    isLoading: isLoading || sendMessageMutation.isPending,
    error: error as Error | null,
  };

  return (
    <ChatContext.Provider value={value}>
      {children}
    </ChatContext.Provider>
  );
};

// 使用聊天hook
export const useChat = () => {
  const context = useContext(ChatContext);
  if (context === undefined) {
    throw new Error('useChat必须在ChatProvider中使用');
  }
  return context;
};